
/*  SYNTH */
#synth {font-family:'Work Sans'}
#synth .settings-preview {
  cursor: pointer;
  display:inline-block;
  position:relative;
  margin:22px;
  display: inline-block;
  width: 72px;
  font-size:12px;
  font-family:'Orbitron';
}
#synth .settings {
  position:relative;
}
#synth .logo {
  text-align:center;
}
#synth .site-link a {
  text-decoration:none;
  color:rgb(41, 92, 204);
  position: absolute;
  right:0;
  top:5px;
  max-width:90px;
}

#synth .site-link a:hover {
  text-decoration:underline;
  color:#CD5700;
}
#synth label {
	display: inline-block;
  margin:5px;
  font-size:15px;
}
#synth label.title {
  font-family: 'Orbitron';
  font-weight:bold;
  font-size:18px;
}
#synth input[type=range] {
	-webkit-transform: rotate(270deg);
	    -ms-transform: rotate(270deg);
	        transform: rotate(270deg);
	display: inline-block;
  height: 100px;
  width: 72px;
}
.range-wrapper {
  position: relative;
  float:left;
  margin-top: 7px;
  text-align: center;
}
.range-wrapper label {
  position:absolute;
  top:94px;
}
input[type=range]::-moz-range-track {
  background-color: rgba(0,0,0,0.3);
}
input[type=range]::-moz-range-thumb {
  background:#fff;
  color: #fff;
  border:1px solid #333;
  box-shadow:-1px 0px 2px #333;
}
#synth select {
	margin-left:10px;
}
#synth button {
	border: 2px solid #333;
    background: none;
	padding: 7px 10px;
  font-family: "Orbitron";
  font-weight:bold;
  border-radius:0px;
  -webkit-box-shadow: 1px 1px 1px #333;
          box-shadow: 1px 1px 1px #333;
  cursor: pointer;
	-webkit-transition:background-color 200ms linear, border-radius 100ms ease-in,-webkit-box-shadow 100ms ease-in;
	transition:background-color 200ms linear, border-radius 100ms ease-in,-webkit-box-shadow 100ms ease-in;
	-o-transition:background-color 200ms linear, border-radius 100ms ease-in,box-shadow 100ms ease-in;
	transition:background-color 200ms linear, border-radius 100ms ease-in,box-shadow 100ms ease-in;
	transition:background-color 200ms linear, border-radius 100ms ease-in,box-shadow 100ms ease-in, -webkit-box-shadow 100ms ease-in;
}
#synth .settings-preview-wrapper button:active {
  background:#fff !important;
  color:#333 !important;
  -webkit-transition:background-color 0ms linear !important;
  -o-transition:background-color 0ms linear !important;
  transition:background-color 0ms linear !important;
}
#synth .settings-preview-wrapper button:hover {
	background:	#CD5700;
  color:#fff;
  border-radius:0px 15px 15px 0px;
  -webkit-transition:background-color 200ms linear, border-radius 300ms ease-in,-webkit-box-shadow 100ms ease-in;
  transition:background-color 200ms linear, border-radius 300ms ease-in,-webkit-box-shadow 100ms ease-in;
  -o-transition:background-color 200ms linear, border-radius 300ms ease-in,box-shadow 100ms ease-in;
  transition:background-color 200ms linear, border-radius 300ms ease-in,box-shadow 100ms ease-in;
  transition:background-color 200ms linear, border-radius 300ms ease-in,box-shadow 100ms ease-in, -webkit-box-shadow 100ms ease-in;
  -webkit-box-shadow: 0px 0px 5px #333;
          box-shadow: 0px 0px 5px #333;
}
#synth .settings-box {
  position:relative;
  background:#fff;
  margin:0 auto;
}
#synth .settings-preview-wrapper {
  display: inline-block;
  vertical-align: top;
  border-right: 2px solid #333;
}
#synth .settings-preview-container {
float:left;
}
#synth .current-settings-preview {
  -webkit-transform:scale(1.4);
      -ms-transform:scale(1.4);
          transform:scale(1.4);
  margin:19px;
}
.synth-toggle-left {
  border-top: 1px solid #333;
  border-bottom: 1px solid #333;
  -webkit-box-shadow: 2px 2px 2px #333;
          box-shadow: 2px 2px 2px #333;
  background:#333;
  color:#fff;
  font-weight:bold;
  cursor:default;
  text-align: center;
  padding: 8px;
  font-size: 12px;
  clear:both;
}
.synth-toggle-right {
  border-top: 1px solid #333;
  border-bottom: 1px solid #333;
  -webkit-box-shadow: 2px 2px 2px #333;
          box-shadow: 2px 2px 2px #333;
  background:#333;
  color:#fff;
  font-weight:bold;
  cursor:default;
  text-align: center;
  padding: 8px;
  font-size: 12px;
  clear:both;
}
#synth .settings-menu-wrapper {
  display: inline-block;
}
#synth .main-settings-bar {
  border-bottom:2px solid #333;
  height:60px;
  font-weight:bold;
  text-shadow: #333
}
#synth .main-settings-bar .title,#synth .main-settings-bar .option {
  display:inline-block;
  padding:6px 20px;
  text-transform: uppercase;

}
#synth .main-settings-bar select {
  font-size:16px;
  padding:0px 5px;
  vertical-align: top;
}
#synth .main-settings-bar .title {
  border-right:2px solid #333;
  padding:12px;
  color:#fff;
  background:#333;
}
#synth .main-settings-bar .title, #synth .main-settings-bar .option,#synth .main-settings-bar label {
  font-size:24px;
}
.touch-keys {position:relative;display: block;clear:both;}
#synth .disclaimer {font-size:12px;}
#visual-settings {
  height:40px;
}
#visual-settings div {
  float:left;
}
#visual-settings input[type=number] {
  width:40px;
}


